import React from 'react'
import { Text, TextProps, Heading } from '@pancakeswap/uikit'
import useTheme from 'hooks/useTheme'

interface HeadingProps extends TextProps {
  text: string
}

const PurpleWordHeading: React.FC<HeadingProps> = ({ text, ...props }) => {
  const { theme } = useTheme()
  const split = text.split(' ')
  const firstWord = split[0]
  const firstWord1 = split[1]
  const firstWord2 = split[2]
  const firstWord3 = split[3] 
  const firstWord4 = split[4] 
  const firstWord5 = split[5] 
  const firstWord6 = split[6] 
  const firstWord7 = split[7] 
  const firstWord8 = split[8] 
  const firstWord9 = split[9] 
  const firstWord10 = split[10] 
  const firstWord11 = split[11] 



  const remainingWords = split.slice(12).join(' ')
  return (
    <Heading  scale="xl" bold mb="24px"  {...props}>
      <span style={{ color: theme.colors.secondary }}>{firstWord} </span>
      <span style={{ color: theme.colors.text }}>{firstWord1} </span>
      <span style={{ color: theme.colors.text }}>{firstWord2} </span>
      <span style={{ color: theme.colors.text }}>{firstWord3} </span>
      <span style={{ color: theme.colors.text }}>{firstWord4} </span>
      <span style={{ color: theme.colors.text }}>{firstWord5} </span>
      <span style={{ color: theme.colors.text }}>{firstWord6} </span>
      <span style={{ color: theme.colors.text }}>{firstWord7} </span>
      <span style={{ color: theme.colors.text }}>{firstWord8} </span> 
      <span style={{ color: theme.colors.gold }}>{firstWord9} </span>
      <span style={{ color: theme.colors.gold }}>{firstWord10} </span>
      <span style={{ color: theme.colors.gold }}>{firstWord11} </span>


      {remainingWords}
    </Heading>
  )
}
 
export default PurpleWordHeading
